<template>
<div>
    <nav id="lg">
        <router-link to="/" title='Torna alla home!' class="nav-item" id="logo">
            <img title="Entity Code" src="../img/logo.png" alt="ENTITY CODE">
        </router-link>
            
        <ul>
            <li id="aboutt" :class="selected === 0 ? 'selected' : ''" @click="changeSelected(0)">
                <router-link to="about" class="nav-item" active-color="orange" reset="white">ABOUT</router-link>
            </li>
            <li :class="selected === 1 ? 'selected' : ''" @click="changeSelected(1)">
                <router-link to="skills" class="nav-item" active-color="#00da00" reset="white">SKILLS</router-link>
            </li>
            <li  :class="selected === 2 ? 'selected' : ''" @click="changeSelected(2)">
                <router-link to="experience" class="nav-item" active-color="#3cebeb" reset="white">EXPERIENCE</router-link>
            </li>
            <li :class="selected === 3 ? 'selected' : ''" @click="changeSelected(3)">
                <router-link to="projects" class="nav-item" active-color="red" reset="white">PROJECTS</router-link>
            </li>
            <li :class="selected === 4 ? 'selected' : ''" @click="changeSelected(4)">
                <router-link to="contacts" class="nav-item" active-color="yellow" reset="white">CONTACTS</router-link>
            </li>
            

        </ul>
    </nav>
            <!-- MARIO -->
            <div @click="marioSound()">
                <router-link to="/" title='Torna alla home!'   class="nav-item" id="go-home" >
                    <img @click="marioSound()" src="../img/mario.png" active-color="white" alt="Ritorna all'inizio">
                    <!-- <i class="fas fa-angle-double-up"></i> -->
                </router-link>
            </div>

    <nav id="xs">

        <router-link to="/" title='Torna alla home!' class="nav-item" id="logo">
            <img readonly title="Entity Code" src="../img/logo.png" alt="ENTITY CODE">
        </router-link>

        <div class="right-xs">

            <span @click="hamburgher()">
                <input class="nav-msg" :size="size" v-model="active" :class="mobile == true ? 'active' : ''" >
            </span>

            <span id="hamburgher" :class="mobile == true ? 'hamburgherRotate' : ''" @click="hamburgher()">
                <img src="../img/mobile.png" width="45px" alt="Menù" title="Menù">
            </span>
        </div>

         <ul id="sub-hamburgher" :class="mobile == true ? 'dFlex' : 'dHidden'">
            <li id="aboutt" :class="selected === 0 ? 'selected' : ''" @click="changeSelected(0)">
                <router-link dataValue="About" to="about" class="nav-item sec" active-color="orange" reset="white">ABOUT</router-link>
            </li>
            <li :class="selected === 1 ? 'selected' : ''" @click="changeSelected(1)">
                <router-link dataValue="Skills" to="skills" class="nav-item sec" active-color="#00da00" reset="white">SKILLS</router-link>
            </li>
            <li :class="selected === 2 ? 'selected' : ''" @click="changeSelected(2)">
                <router-link dataValue="Experience" to="experience" class="nav-item sec" active-color="#3cebeb" reset="white">EXPERIENCE</router-link>
            </li>
            <li :class="selected === 3 ? 'selected' : ''" @click="changeSelected(3)">
                <router-link dataValue="Projects" to="projects" class="nav-item sec" active-color="red" reset="white">PROJECTS</router-link>
            </li>
            <li :class="selected === 4 ? 'selected' : ''" @click="changeSelected(4)">
                <router-link dataValue="Contacts" to="contacts" class="nav-item sec" active-color="yellow" reset="white">CONTACTS</router-link>
            </li>
            
            

        </ul>
            
    </nav>
</div>


</template>

<script>


export default {
    data () {
        return {
            selected: 0,
            mobile: false,
            active: 'Menù',
            size: 4,
        }
    },
    mounted: function (){
        this.changeColor();   
    },
    methods: { 
        changeSelected: function(i) {
            this.selected = i;
            this.mobile = false;
            let items = document.querySelectorAll(".sec");
            let itemValue = items[i].getAttribute('dataValue');
            let itemColor = items[i].getAttribute('active-color');
            let input = document.querySelector('.nav-msg');
            this.active = items[i].getAttribute('dataValue');
            this.size = itemValue.length;
            input.style.color = itemColor;
        },
        changeColor: function () {
            //color effect
            "use strict";
            var items = document.querySelectorAll('.nav-item');

            function handleIndicator(el) {
                items.forEach(function (item) {
                    item.classList.remove('is-active');
                    item.removeAttribute('style');
                });
                el.classList.add('is-active');
                el.style.color = el.getAttribute('active-color');
            }
            items.forEach(function (item) {
                item.addEventListener('click', function (e) {
                    handleIndicator(e.target);
                });
                item.classList.contains('is-active') && handleIndicator(item);
            });   
            items.forEach(function (item) {

                item.classList.remove('is-active');
                item.removeAttribute('style');
                item.style.color = item.getAttribute('reset');
            })
        },
        marioSound: function () {
            //reset al menù bianco (nav-msg)
            let input = document.querySelector('.nav-msg');
            this.mobile = false;
            this.size = 4;
            this.active = "Menù";
            input.style.color = "#fff";
            var audio = new Audio('../music/mario.mp3');
            console.log(audio);
            audio.volume = 0.1;
            audio.play();
        },
        hamburgher: function () {
            this.mobile = !this.mobile;
        }
    }
}
</script>


