<template>
    <div id="music">

        <!-- controls -->
        <div id="controls-container">

            
            
            <!-- msg -->
            <span id="msg-music">Un pò di musica? -></span>

            <!-- playbtn -->
            <i title='Play' v-if="playing == false" @click="play()" class="far fa-play-circle play-btn"></i>
            <i title='Stop' v-else @click="play()" class="far fa-stop-circle play-btn"></i>

            <br>
            

            <audio loop controls id="audio" class="fadeOut">
                <source src="../music/lofi.mp3" type="audio/mp3">
            </audio>
            <!-- arrow left & right -->
            <button title='Impostazioni musica' class="arrow-controls" v-if="controls == false" @click="showControls()">
                <i class="fas fa-caret-left" ></i>
            </button>
            <button class="arrow-controls" v-else @click="showControls()">
                <i class="fas fa-caret-right"></i>
            </button>
            

        </div>

        

        

    </div>

    
</template>


<script>

export default ({
    data () {
        return {
            playing: false,
            controls: false
        }
    },
    mounted: function () {
        
        
        this.msg();
        
    },
    methods: {
        //music
        play: function() {

            var audio = document.querySelector('#audio');
            audio.volume = 0.3;
            this.playing = !this.playing;
            this.playing == true ? audio.play() : audio.pause();     
        },
        msg: function() {

            setTimeout(function(){
                var msg = document.querySelector('#msg-music');
                msg.className = "dBlock";
                msg.className = "hidden";
                setTimeout(function(){msg.className = "dNone";}, 5000);
            }, 5000);

            setInterval(function(){
                var msg = document.querySelector('#msg-music');
                msg.className = "position";
                setTimeout(function(){msg.className = "visible";}, 0)              
                setTimeout(function(){msg.className = "hidden";}, 5000)
            }, 100000);
        },
        showControls: function() {
            var settings = document.querySelector('#audio');
            this.controls = !this.controls;
            
            if (this.controls == true) {
                settings.className = "fadeIn";
            } else {
                settings.className = "fadeOut";
            }
            
        }
        
    }
});
</script>
