<template>
    <div>
        <div class="carousel-cont">
            <span class="btn prev">&lt;</span><span class="btn next">&gt;</span>
            <div class="scroller">
                <div class="item">Item 1</div>
                <div class="item">Item 2</div>
                <div class="item">Item 3</div>
                <div class="item">Item 1</div>
                <div class="item">Item 2</div>
                <div class="item">Item 3</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'app',
  
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function (){
    console.log('Carousel mounted');
    this.scroll();
  },
  methods: {
    sayHello: function () {
      console.log("Carousel Mounted");
    },
    scroll: function () {
        const testimonials = document.querySelector('.carousel-cont');
        const scroller = testimonials.querySelector('.scroller');
        const nextBtn = testimonials.querySelector('.btn.next');
        const prevBtn = testimonials.querySelector('.btn.prev');
        const itemWidth = testimonials.querySelector('.item').clientWidth;
        nextBtn.addEventListener('click', scrollToNextItem);
        prevBtn.addEventListener('click',scrollToPrevItem); 
        function scrollToNextItem() {
          scroller.scrollBy({left: itemWidth,top: 0,behavior: 'smooth'});
        }
        function scrollToPrevItem() {
          scroller.scrollBy({left: -itemWidth,top: 0, behavior: 'smooth'});
        }
    }
  }
}
</script>